g毕业于香港财经大学，曾任职。"),_c('br'),_vm._v("\n      About our strategy。"),_c('br'),_vm._v("\n      道氏理论、波浪理论、斐波那契数列、特殊K线形态、MACD、KDJ等，仍是我们技术分析的基础和依据，当然我们对这些进行了重要的优化和改进。很多投资者不相信图表，但对我们而言，图表反应市场的一切，这是威观公司最重要的理念和原则。解读图表信息，结合市场人气，寻找趋势性交易的机会，是我们一直致力之事。。"),_c('br'),_vm._v(" "),_c('img',{attrs:{"src":require("../assets/images/about2.png"),"alt":"图片"}}),_vm._v("\n      假设市场波动模型如上左图"),_c('br'),_vm._v("\n      对一段完整的下跌结构而言，找到并提示图示S1、S2、S3的卖点，对已有头寸的持仓者无比重要，在这个点卖出，可以避开后面的回调导致的惨重损失。"),_c('br'),_vm._v("\n      同样，对一段完整的上涨结构而言，如上右图"),_c('br'),_vm._v("\n      找到B1、B2、B3这些买点并建立做多头寸，无疑能获得不错的收益，而如果在这个上涨趋势中，根据短暂调整的S1、S2、S3点进行做差价的加减仓，无疑将提高资金的使用效率而获得最理想的投资收益。"),_c('br'),_vm._v("\n      找到并分析这些B点、S点，是英国威观公司提供咨询服务的核心所在。"),_c('br'),_vm._v("\n      About our convictions"),_c('br'),_vm._v("\n      我们的信念"),_c('br'),_vm._v("\n      保持客观、保持谨慎、务需言之有物"),_c('br'),_vm._v("\n      保持独立、保持热情、务必有所启示"),_c('br'),_vm._v("\n      值得注意别需特别提出的是，技术图表买卖点的研究是威观公司的专长之处。技术分析从来是有效的，但不是万能的。为提高胜算，投资者仍需自行研究个股的基本因素，综合决策到底是卖出还是买进。"),_c('br'),_vm._v("\n      这些需关注的基本因素（CANSLIM）涵盖以下方面：最近一个季度报表显示盈利情况，每年都每股盈利的增幅，新产品新管理方法，该股票流通盘大小、市值及交易量情况，该股票在行业中的地位、是否行业龙头，该股票有无强力控盘的庄家或机构大股东，以及大盘趋势等等，这些情况综合我们的技术图表分析，才可以获得较全面的投资判断。"),_c('br')])])])}
